import React, { Component } from 'react'
import TextField from 'material-ui/TextField'
import SelectField from 'material-ui/SelectField'
import MenuItem from 'material-ui/MenuItem'
import ImageResult from './imageResult'
import axios from 'axios'

export default class search extends Component {
    state = {
        text: '',
        amount: '10',
        apiurl: "https://pixabay.com/api/",
        apikey: '9915405-432536e36f42f142bbbfa3a85',
        images: []
    };
    onTextChange = (e) => {
        const val = e.target.value;
        this.setState({
            [e.target.name]: val
        }, () => {
            if (val === '') {
                this.setState({
                    images: []
                })
            } else {
                axios.get(`${this.state.apiurl}?key=${this.state.apikey}&q=${this.state.text}&image_type=photo&per_page=${this.state.amount}&safesearch=false`).then(res => {
                    this.setState({
                        images: res.data.hits
                    })
                }).catch(err => console.log(err))
            }

        })
    }
    onAmountChange = (e, index, value) => this.setState({ amount: value })
    render() {

        return (
            <div>
                <TextField name="text" value={this.state.text} onChange={this.onTextChange} floatingLabelText="Search For images" fullWidth={true} />
                <br />
                <SelectField name="amount" floatingLabelText="Amount" value={this.state.amount}
                    onChange={this.onAmountChange}>
                    <MenuItem value="5" primaryText="5" />
                    <MenuItem value="10" primaryText="10" />
                    <MenuItem value="15" primaryText="15" />
                    <MenuItem value="20" primaryText="20" />
                </SelectField>
                {this.state.images.length > 0 && <ImageResult image={this.state.images} />}

            </div>
        )
    }
}



