import React, { Component } from 'react';
import Navbar from './components/nabar'
import MultiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import Search from './components/search'

import './App.css';

class App extends Component {
  render() {
    return (
      <MultiThemeProvider>
        <div>
          <Navbar />
          <Search />
        </div>
      </MultiThemeProvider>
    );
  }
}

export default App;



