import React, { Component } from 'react'
import { GridTile, GridList } from 'material-ui/GridList'
import IconButton from 'material-ui/IconButton'
import Dialog from 'material-ui/Dialog'
import ZoomIn from 'material-ui/svg-icons/action/zoom-in'
import FlagButton from 'material-ui/FlatButton'

export default class ImageResult extends Component {
    state = {
        open: false,
        currentImg: ''
    }
    handleOpen = img => {
        this.setState({
            open: true,
            currentImg: img
        })
    }
    handleClose = () => {
        this.setState({
            open: false
        })
    }
    render() {
        const { image } = this.props;
        const action = [<FlagButton label="close" primary={true} onClick={this.handleClose} />]

        let imageContent = (<GridList cols={3}>
            {image.map(img => (<GridTile title={img.tags}
                key={img.id}
                subtitle={<span>
                    by<strong>{img.user}</strong>
                </span>}

                actionIcon={<IconButton onClick={() => this.handleOpen(img.largeImageURL)}>
                    <ZoomIn color="white" />
                </IconButton>}>
                <img src={img.largeImageURL} />
            </GridTile>)

            )}
        </GridList>)
        return <div>
            {imageContent}
            <Dialog actions={action}
                open={this.state.open}
                onRequestClose={this.handleClose}
            >
                <img src={this.state.currentImg} style={{
                    width: '100%',
                    height: '400px'
                }} />

            </Dialog>

        </div>
    }
}



